import useProfileXCetusStore from '@/store/profile/xcetus'
import useXCetusStore from '@/store/xcetus/useXCetus'
import { Block } from '@cetus/design'
import useCountdown from '@cetus/hooks/src/useCountdown'
import { useAccountStore } from '@cetus/stores'
import { formatCurrency, formatPercentage } from '@cetus/utils'
import { Divider, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import Header from './Header'

type Props = {
  cetusApr: string
  myShare: string
  totalRewardValue: string
  nextStartTime: number | undefined
}

function Rewards({ cetusApr, myShare, totalRewardValue, nextStartTime }: Props) {
  const { currentAccount } = useAccountStore()
  const { days, hours, minutes, seconds } = useCountdown((nextStartTime || 0) * 1000)
  const { setCurrentXCetusTab, setIsXCetusModalOpen } = useProfileXCetusStore()
  const { veNFTLoading } = useXCetusStore()
  const onClickClaim = () => {
    setIsXCetusModalOpen(true)
    setCurrentXCetusTab('Get xCETUS')
  }
  return (
    <Block flex="1.46" p="16px" borderRadius="16px" bg="block_color_opacity.50" backdropFilter="blur(20px)">
      <VStack w="100%" gap="0">
        <Header title="Staking Rewards" onClick={onClickClaim} btnTitle="Claim" />
        <VStack m="36px 0 50px" h="57px">
          <HStack>
            <Time value={days} type="d" />
            <Text color="text_caption">:</Text>
            <Time value={hours} type="h" />
            <Text color="text_caption">:</Text>
            <Time value={minutes} type="m" />
            <Text color="text_caption">:</Text>
            <Time value={seconds} type="s" />
          </HStack>
          <Text fontSize="14px" h="17px" lineHeight="17px">
            Reward Distribution In
          </Text>
        </VStack>
        <HStack justify="space-between" w="80%" mb="12px">
          <Item title="Est.APR" value={`â‰ˆ${formatPercentage(cetusApr, 2)}`} loading={cetusApr === '0'} />
          <Divider orientation="vertical" />
          <Item title="Your Share" value={myShare !== '--' ? `â‰ˆ${myShare}` : myShare} loading={veNFTLoading} />
          <Divider orientation="vertical" />
          <Item title="Pending Yield" value={currentAccount ? formatCurrency(totalRewardValue, 2) : '--'} loading={veNFTLoading} />
        </HStack>
      </VStack>
    </Block>
  )
}
type TimeType = 'd' | 'h' | 'm' | 's'
const TimeMap: Record<TimeType, string> = {
  d: 'D',
  h: 'H',
  m: 'M',
  s: 'S'
}
type TimeProps = {
  value: number
  type: TimeType
}
const Time = ({ value, type }: TimeProps) => {
  return (
    <HStack borderRadius="8px" bg="background" p="4px 8px" justify="center" align="flex-end" w="64px" h="32px" gap="2px">
      <Text color="text_caption" fontSize="20px" textAlign="center">
        {value}
      </Text>

      <Text fontSize="12px" mb="1px">
        {TimeMap[type]}
      </Text>
    </HStack>
  )
}

type ItemProps = {
  title: string
  value: string
  loading: boolean
}
const Item = ({ title, value, loading }: ItemProps) => {
  return (
    <VStack gap="3px">
      <Skeleton isLoaded={!loading} h="17px">
        <Text color="text_caption" fontSize="14px" h="17px" lineHeight="1">
          {value}
        </Text>
      </Skeleton>
      <Text fontSize="14px" h="15px" lineHeight="1">
        {title}
      </Text>
    </VStack>
  )
}
export default Rewards
