import ProfileOrders from '@/components/profile/Orders'
import ProfileHeader from '@/components/profile/ProfileHeader'
import ProfileLiquidity from '@/components/profile/ProfileLiquidity'
import ProfileWalletHoldings from '@/components/profile/ProfileWallet/ProfileWalletHoldings'
import XCetus from '@/components/profile/XCetus'
import { useGetActivityTvl } from '@/hooks/profile/useGetActivityTvl'
import { useGetProfileLiquidityTvl } from '@/hooks/profile/useGetProfileLiquidityTvl'
import { useGetXCetusTvl } from '@/hooks/profile/useGetXCetusTvl'
import { useProfileTask } from '@/hooks/profile/useProfileTask'
import useLimitListStore from '@/store/limit/useLimitList'
import usePositionStore from '@/store/position'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import useWalletHoldingsStore from '@/store/profile/walletHoldings'
import useXCetusStore from '@/store/xcetus/useXCetus'
import { ProfileTab } from '@/types/profile'
import { Block } from '@cetus/design'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import { useAccountStore } from '@cetus/stores'
import { NoData } from '@cetus/ui-kit'
import { d, formatCurrency } from '@cetus/utils'
import { HStack, Image, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo } from 'react'
import { useNavigate } from 'react-router-dom'

const formatUsdValue = (value?: number | string) => (value ? formatCurrency(value, 2) : '$--')

const getTotalValue = (...values: (string | number | undefined)[]) => {
  return values.reduce((acc, val) => acc.plus(val && +val ? val : 0), d(0)).toString()
}
function ProfilePage() {
  const { tab } = useQueryParams()
  const { currentAccount, onWalletModal } = useAccountStore()
  const navigate = useNavigate()

  const { posBaseListLoading, posLiquidityDataLoading } = usePositionStore()
  const { holdingsTotalUsd, isCoinPriceLoading, isCoinHoldingLoading } = useWalletHoldingsStore()
  const { clmmTotalTvl } = useGetProfileLiquidityTvl()
  const { orderTotalTvl } = useGetActivityTvl()
  const { orderListLoading } = useLimitListStore()
  const { isAutoRefresh, dcaOrderListLoading, setAutoRefreshCount, resetAutoRefreshCount } = useActiveOrdersStore()
  const { xCetusTotalTvl } = useGetXCetusTvl()
  const { lockCetusListLoading, veNFTLoading, veNFT } = useXCetusStore()

  const profileTab = useMemo(() => tab || 'wallet', [tab])

  const { refreshTask } = useProfileTask(profileTab as ProfileTab)

  const walletLoading = !isAutoRefresh && (isCoinHoldingLoading || isCoinPriceLoading)
  const liquidityLoading = !isAutoRefresh && (posBaseListLoading || posLiquidityDataLoading)
  const ordersLoading = !isAutoRefresh && (orderListLoading || dcaOrderListLoading)
  const xCetusLoading = !isAutoRefresh && (lockCetusListLoading || veNFTLoading)

  const tabDataObj = useMemo(() => {
    console.log('ðŸš€ ~ ProfilePage ~ holdingsTotalUsd', holdingsTotalUsd, clmmTotalTvl, orderTotalTvl, xCetusTotalTvl)
    return {
      wallet: { totalValue: formatUsdValue(holdingsTotalUsd), isLoading: walletLoading },
      liquidity: { totalValue: formatUsdValue(clmmTotalTvl), isLoading: liquidityLoading },
      orders: { totalValue: formatUsdValue(orderTotalTvl), isLoading: ordersLoading },
      xCetus: { totalValue: formatUsdValue(xCetusTotalTvl), isLoading: xCetusLoading }
    }
  }, [holdingsTotalUsd, clmmTotalTvl, orderTotalTvl, xCetusTotalTvl, walletLoading, liquidityLoading, ordersLoading, xCetusLoading])

  const profileTabList = [
    { title: 'Wallet Holdings', value: 'wallet', route: '/profile?tab=wallet', activeImgUrl: '/images/wallet_tab.png' },
    { title: 'Liquidity', value: 'liquidity', route: '/profile?tab=liquidity', activeImgUrl: '/images/liquidity_tab.png' },
    { title: 'Active Orders', value: 'orders', route: '/profile?tab=orders', activeImgUrl: '/images/order_tab.png' },
    { title: 'xCETUS', value: 'xCetus', route: '/profile?tab=xCetus', activeImgUrl: '/images/xCetus_tab.png' }
  ]

  const totalValue = useMemo(
    () => getTotalValue(xCetusTotalTvl, clmmTotalTvl, orderTotalTvl, holdingsTotalUsd),
    [xCetusTotalTvl, clmmTotalTvl, orderTotalTvl, holdingsTotalUsd]
  )

  useEffect(() => resetAutoRefreshCount(false), [])

  return (
    <VStack minW="1200px" p="40px 20px 0" margin="0px auto" gap="12px" alignItems="start">
      {!currentAccount?.address ? (
        <NoData type="nowallet" mt="20px" onboard={() => onWalletModal(true)} />
      ) : (
        <VStack w="100%" gap="12px" alignItems="start">
          <ProfileHeader
            handleRefresh={isManual => {
              setAutoRefreshCount(!isManual)
              refreshTask()
            }}
            totalValue={totalValue}
            isLoading={!isAutoRefresh && (veNFTLoading || posBaseListLoading || orderListLoading || dcaOrderListLoading)}
          />
          <ProfileTabBlock
            tabList={profileTabList}
            activeTab={profileTab}
            tabData={tabDataObj}
            onClickTab={(value: string) => navigate(`/profile?tab=${value}`)}
          />
          {profileTab === 'wallet' && <ProfileWalletHoldings />}
          {profileTab === 'liquidity' && <ProfileLiquidity />}
          {profileTab === 'orders' && <ProfileOrders />}
          {profileTab === 'xCetus' && <XCetus />}
        </VStack>
      )}
    </VStack>
  )
}

function ProfileTabBlock({
  tabList,
  activeTab,
  tabData,
  onClickTab
}: {
  tabList: any
  activeTab: string
  tabData: any
  onClickTab: (value: string) => void
}) {
  return (
    <HStack w="100%" mt="10px">
      {tabList.map((item: any) => (
        <Block
          key={item.value}
          w="25%"
          p="24px 16px"
          h="94px"
          bgImage={activeTab === item.value ? 'url(/images/tab_active.png)' : 'none'}
          bgSize="100% 100%"
          borderRadius="16px"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          cursor="pointer"
          bg="block_color_opacity.50"
          backdropFilter="blur(20px)"
          onClick={() => onClickTab(item.value)}
        >
          <VStack align="flex-start" gap="12px">
            <Text fontSize="14px" color="text_caption">
              {item.title}
            </Text>
            <HStack h="22px">
              <Skeleton isLoaded={!tabData[item.value]?.isLoading}>
                <Text fontSize="22px" fontWeight="500" color="text_caption">
                  {tabData[item.value]?.totalValue}
                </Text>
              </Skeleton>
            </HStack>
          </VStack>
          <Image w="48px" h="48px" src={item.activeImgUrl} display={activeTab === item.value ? 'block' : 'none'} />
        </Block>
      ))}
    </HStack>
  )
}

export default ProfilePage
