import { SingleCoinImage } from '@cetus/ui-kit'
import { Box, Button, HStack, Text } from '@chakra-ui/react'
type Props = {
  title: string
  imgUrl?: string
  onClick?: () => void
  btnTitle?: string
}
function Header({ title, imgUrl, onClick, btnTitle }: Props) {
  return (
    <HStack justify="space-between" align="flex-start" w="100%">
      <HStack>
        {imgUrl && (
          <SingleCoinImage
            imageUrl={imgUrl}
            p="4px"
            imgBoxStyle={{
              w: '20px',
              h: '20px',
              bg: 'block_color',
              borderColor: 'transparent',
              borderRadius: '20px'
            }}
          />
        )}
        <Text>{title}</Text>
      </HStack>
      {onClick ? (
        <Button h="32px" borderRadius="8px" p="8px" fontSize="12px" onClick={onClick} fontWeight="500">
          {btnTitle}
        </Button>
      ) : (
        <Box h="32px" />
      )}
    </HStack>
  )
}

export default Header
