import usePositionStore from '@/store/position'
import useXCetusStore from '@/store/xcetus/useXCetus'
import { PosBaseInfo } from '@/types/position'
import { useAccountStore } from '@cetus/stores'
import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import envConfigs from '@cetus/types/src/config/envConfigs'
import usePosHelper from '../position/usePosHelper'
import usePositionList from '../position/usePositionList'
import { buildLockCetus, buildVeNFT, XCetusLockCetusType, XCetusVeNFTType } from '../xcetus/useXCetusHelper'

export function useOwnerNFT() {
  const { currentAccount } = useAccountStore()
  const { clmmSdk } = useClmmSDKStore()
  const { xCetusSdk } = usePeripherySDKStore()
  const { buildFarmsPositionType, buildBurnPositionType } = usePosHelper()
  const { setPosBaseList, setFullRangePosBaseList, setPosBaseListLoading } = usePositionStore()
  const { setVeNFT, setVeNFTLoading, setLockCetusList, clearData, owner, setLockCetusListLoading } = useXCetusStore()
  const { formatPosBaseList } = usePositionList()

  /**
   *  1: èŽ·å–ä»“ä½åˆ—è¡¨
   *  2: èŽ·å–xcetus  veNFT
   *  3: èŽ·å–xCetus é”ä»“ä¿¡æ¯
   */
  const fetchOwnerNFT = async () => {
    try {
      if (currentAccount) {
        setPosBaseListLoading(true)
        setVeNFTLoading(true)
        const ownerRes = await clmmSdk.fullClient.getOwnedObjectsByPage(currentAccount.address, {
          options: { showType: true, showContent: true, showOwner: true },
          filter: {
            MatchAny: [
              {
                Package: envConfigs.clmm_sdk_options.clmm_pool.package_id
              },
              {
                StructType: buildFarmsPositionType
              },
              {
                StructType: buildBurnPositionType
              },
              {
                StructType: XCetusVeNFTType
              },
              {
                StructType: XCetusLockCetusType
              }
            ]
          }
        })

        // ä»“ä½åˆ—è¡¨
        let result: PosBaseInfo[] = await formatPosBaseList(ownerRes)
        setPosBaseList(result)
        setPosBaseListLoading(false)
        if (result?.length == 0) {
          setFullRangePosBaseList([])
        }

        // èŽ·å–xCetus  veNFT
        const veNFT = buildVeNFT(ownerRes.data)
        if (veNFT) {
          setVeNFT(veNFT, currentAccount.address)

          if (owner === currentAccount.address) {
            // èŽ·å–xCetus é”ä»“ä¿¡æ¯
            const lockList = await buildLockCetus(ownerRes.data, xCetusSdk)
            console.log('ðŸš€ ~ fetchOwnerNFT ~ lockList:', lockList)
            setLockCetusList(lockList)
          }
        } else {
          // æ²¡ç”¨veNFTï¼Œè®¾ç½®é”ä»“åˆ—è¡¨loadingä¸ºfalse
          setLockCetusListLoading(false)
        }

        return {
          veNFT,
          posBaseList: result
        }
      }
    } catch (error) {
      console.error('ðŸš€ ~ fetchOwnerNFT ~ error:', error)
      if (error instanceof TypeError) {
        setPosBaseListLoading(false)
        setPosBaseList([])
      }
    } finally {
      setPosBaseListLoading(false)
      setVeNFTLoading(false)
    }
    return {
      veNFT: undefined,
      posBaseList: []
    }
  }

  const resetUserData = () => {
    setPosBaseList([])
    clearData()
  }

  return { fetchOwnerNFT, resetUserData }
}
