import DcaItemCoinPirBlock from '@/components/dca/DcaItemBlock/DcaItemCoinPirBlock'
import DcaProgressBlock from '@/components/dca/DcaItemBlock/DcaProgressBlock'
import DcaTableAction from '@/components/dca/DcaItemBlock/DcaTableAction'
import RangeValueBlock from '@/components/dca/DcaItemBlock/RangeValueBlock'
import useDcaActions from '@/hooks/dca/useDcaActions'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import { SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { H5MapTable, NoData, Table } from '@cetus/ui-kit'
import { d, formatNumber, utcTimeFormatted } from '@cetus/utils'
import { Button, HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'

function Dca() {
  const { currentAccount } = useAccountStore()
  const { isAutoRefresh, dcaOrderListLoading, dcaActiveOrderList, dcaPastOrderList } = useActiveOrdersStore()

  const [currentTab, setCurrentTab] = useState('activeDCAs')
  const tabList = useMemo(() => {
    return [
      {
        label: 'Active DCAs',
        value: 'activeDCAs',
        num: !isAutoRefresh && dcaOrderListLoading ? '' : dcaActiveOrderList?.length
      },
      {
        label: 'Past DCAs',
        value: 'pastDCAs',
        num: !isAutoRefresh && dcaOrderListLoading ? '' : dcaPastOrderList?.length
      }
    ]
  }, [dcaOrderListLoading, dcaPastOrderList, dcaActiveOrderList])

  const currentList = useMemo(() => {
    if (currentTab == 'activeDCAs') {
      return dcaActiveOrderList
    } else {
      return dcaPastOrderList
    }
  }, [currentTab, dcaActiveOrderList, dcaPastOrderList])

  const { isApp } = useWindowWidth()
  const { closeAll, claimAll, isClaimAllLoading, isCloseAllLoading } = useDcaActions()

  const totalClaimNum = useMemo(() => {
    if (currentTab === 'pastDCAs' || currentList?.length === 0) return 0
    return currentList?.reduce((sum, orderInfo: any) => d(sum).plus(orderInfo?.outBalance as string), d(0)).toString()
  }, [currentTab, currentList])

  return (
    <VStack w="100%">
      <HStack w="100%" justify="space-between">
        <SelectTab
          type="outlineTab"
          tabList={tabList}
          currentTab={currentTab}
          handleChangeTab={(item: any) => setCurrentTab(item?.value)}
          isActive={(current, tab) => current === tab.value}
          wrapStyle={{
            border: 'none',
            h: '32px',
            gap: '8px'
          }}
          itemStyle={{
            fontSize: '14px',
            borderRadius: '8px',
            p: '0 8px'
          }}
        />
        {currentList?.length > 1 && currentTab == 'activeDCAs' && (
          <HStack>
            <Button
              h="32px"
              p="8px"
              fontSize="12px"
              fontWeight="500"
              borderRadius="8px"
              isLoading={isClaimAllLoading}
              disabled={Number(totalClaimNum) === 0 || isClaimAllLoading}
              onClick={() => {
                claimAll(currentList)
              }}
            >
              Claim All
            </Button>
            <Button
              h="32px"
              p="8px"
              fontSize="12px"
              fontWeight="400"
              variant="ghost"
              isLoading={isCloseAllLoading}
              disabled={currentList?.length == 0 || isCloseAllLoading}
              onClick={() => {
                closeAll(currentList)
              }}
            >
              Close All
            </Button>
          </HStack>
        )}
      </HStack>
      <VStack w="100%" position="relative" gap="20px">
        {!dcaOrderListLoading && currentList?.length === 0 ? (
          <NoData type="nodata" noBorder />
        ) : isApp ? (
          <VStack w="100%" gap="4px">
            <H5MapTable
              rowKey="orderId"
              columns={getColumns(currentTab == 'activeDCAs')}
              dataSource={currentList}
              loading={!isAutoRefresh && dcaOrderListLoading}
              itemSkeletonLength={5}
              itemHeight="30px"
            />
          </VStack>
        ) : (
          <Table
            rowKey="orderId"
            columns={getColumns(currentTab == 'activeDCAs')}
            dataSource={currentList}
            loading={!isAutoRefresh && dcaOrderListLoading}
            rowStyle={{ h: '80px', cursor: 'pointer' }}
          />
        )}
      </VStack>
    </VStack>
  )
}

const getColumns = (isActiveOrder: boolean) => {
  const columns = [
    {
      title: <Text>Orders</Text>,
      key: 'order',
      thConfig: { w: '18%' },
      render: (record: any) => <DcaItemCoinPirBlock orderInfo={record} />
    },
    {
      title: <Text textAlign="right">Price Range</Text>,
      key: 'price',
      thConfig: { w: '20%' },
      render: (record: any) => <RangeValueBlock orderInfo={record} isProfile={true} isRank={true} />
    },
    {
      title: <Text textAlign="right">Total</Text>,
      key: 'total',
      thConfig: { w: '12%' },
      render: ({ inDeposited, inCoin }: { inDeposited: string; inCoin: Token }) => (
        <Text color="text_caption">
          {formatNumber(inDeposited)} {inCoin?.symbol}
        </Text>
      )
    },
    {
      title: <Text textAlign="right">Completed</Text>,
      key: 'completed',
      thConfig: { w: '15%' },
      render: (record: any) => <DcaProgressBlock orderInfo={record} isProfile={true} />
    },
    isActiveOrder && {
      title: <Text textAlign="right">Next Order (UTC)</Text>,
      key: 'nextOrder',
      thConfig: { w: '20%' },
      render: ({ nextCycleAtTimeStamp }: { nextCycleAtTimeStamp: string }) => (
        <Text color="text_caption">{utcTimeFormatted(nextCycleAtTimeStamp)}</Text>
      )
    },
    {
      title: <Text textAlign="right">{isActiveOrder ? 'Actions' : 'Action'}</Text>,
      key: 'actions',
      thConfig: { w: '15%' },
      render: (record: any) => <DcaTableAction orderInfo={record} isActiveOrder={isActiveOrder} />
    }
  ]

  return columns.filter(Boolean)
}

export default Dca
