import XCetusToCetus from '@/components/xcetus/XCetusToCetus'
import useXCetusCancelAction from '@/hooks/xcetus/useXCetusCancelAction'
import useXCetusClaimAction from '@/hooks/xcetus/useXCetusClaimAction'
import useXCetusStore from '@/store/xcetus/useXCetus'
import { Block } from '@cetus/design'
import { Table } from '@cetus/ui-kit'
import { ColumnsType } from '@cetus/ui-kit/src/components/Table'
import { LockCetus, XCetusUtil } from '@cetusprotocol/cetus-periphery-sdk'
import { Button, Divider, HStack, Text } from '@chakra-ui/react'
import dayjs from 'dayjs'
import { useEffect, useState } from 'react'
import ProfileMenus from '../ProfileMenus'

function RedeemList() {
  const { lockCetusList, lockCetusListLoading } = useXCetusStore()
  return (
    lockCetusList &&
    lockCetusList.length > 0 && (
      <Block p="0 16px 16px" w="100%" bg="block_color_opacity.50" backdropFilter="blur(20px)">
        <ProfileMenus type="title" title="Redeeming xCETUS" />
        <Divider orientation="horizontal" />
        <Table<LockCetus>
          mt="10px"
          dataSource={lockCetusList}
          columns={getColumns()}
          loading={lockCetusListLoading}
          skeletonLength={lockCetusList?.length}
          rowStyle={{ h: '80px' }}
        />
      </Block>
    )
  )
}

const getColumns = (): ColumnsType<LockCetus>[] => {
  return [
    {
      title: <Text>xCETUS To CETUS</Text>,
      key: 'to',
      render: record => {
        return <XCetusToCetus xcetus_amount={record.xcetus_amount} cetus_amount={record.cetus_amount} imageSize="20px" />
      }
    },
    {
      title: <Text textAlign="right">Vesting Duration (Day)</Text>,
      key: 'lock_day',
      render: record => (
        <Text color="text_caption" fontSize="14px">
          {record?.lock_day}
        </Text>
      )
    },
    {
      title: <Text textAlign="right">Available Claim Date</Text>,
      key: 'date',
      render: record => {
        return (
          <Text color="text_caption" fontSize="14px">
            {dayjs.unix(record?.locked_until_time).format('MMM D, YYYY HH:mm')}
          </Text>
        )
      }
    },
    {
      title: <Text textAlign="right">Actions</Text>,
      key: 'actions',
      render: record => {
        const [isCanClaim, setIsCanClaim] = useState<boolean>(false)
        const { cancelOrderLoading, handleCancelOrder } = useXCetusCancelAction()
        const { claimOrderLoading, handleClaimOrder } = useXCetusClaimAction()
        const handleButtonClick = (isClickClaim: boolean) => {
          if (isClickClaim) {
            handleClaimOrder(record.id)
          } else {
            handleCancelOrder(record)
          }
        }

        useEffect(() => {
          setIsCanClaim(!XCetusUtil.isLocked(record))
        }, [])
        return (
          <HStack justify="flex-end">
            <Button
              onClick={() => {
                if (cancelOrderLoading || claimOrderLoading) {
                  return
                }
                handleButtonClick(isCanClaim)
              }}
              variant="outline"
              h={{ base: '40px', lg: '28px' }}
              w={{ base: '168px', lg: '80px' }}
              borderRadius={{ base: '12px', lg: '8px' }}
              fontSize="14px"
              isLoading={cancelOrderLoading || claimOrderLoading}
              color={isCanClaim ? 'text_highlight' : 'primary_gray'}
              bg="button_ghost_bg"
            >
              {isCanClaim ? 'Claim' : 'Cancel'}
            </Button>
          </HStack>
        )
      }
    }
  ]
}

export default RedeemList
