import usePositionStore from '@/store/position'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import { Block } from '@cetus/design'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { NoData } from '@cetus/ui-kit'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Box, HStack, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import PoolsFilter from '../pools/PoolsFilter'
import LiquidityAndYield from '../position/list/LiquidityAndYield'
import PoolItem from '../position/list/PoolItem'
import PositionListLoading from '../position/list/PositionListLoading'
import ProfileMenus from './ProfileMenus'

function ProfileLiquidity() {
  const { currentAccount, onWalletModal } = useAccountStore()
  const { showPosListLength, setShowPosListLength, posBaseListLoading, posBaseListGroupByPool } = usePositionStore()
  const [showPosListGroupByPool, setShowPosListGroupByPool] = useState<any>([])
  const { isAutoRefresh } = useActiveOrdersStore()

  const [selectCoinList, setSelectCoinList] = useState<Token[]>([])
  const onClickSelectCoinList = (tokenInfo: Token) => {
    console.log('ðŸš€ ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => [...prev, tokenInfo])
  }
  const onDeleteSelectCoinList = (tokenInfo: Token) => {
    console.log('ðŸš€ ~ onClickIncentiveTypes ~ tokenInfo:', tokenInfo)
    setSelectCoinList(prev => prev.filter(ele => ele?.coin_type !== tokenInfo?.coin_type))
  }
  useEffect(() => {
    if (currentAccount?.address) {
      console.log('ðŸš€ ~ useEffect ~ currentAccount?.address:', currentAccount?.address)
    } else {
      setShowPosListLength('')
    }
  }, [currentAccount?.address])

  useEffect(() => {
    const firstTokenAddress = selectCoinList[0]?.coin_type ? fixCoinType(selectCoinList[0]?.coin_type) : undefined
    const lastTokenAddress = selectCoinList[1]?.coin_type ? fixCoinType(selectCoinList[1]?.coin_type) : undefined
    const filterArr = Object.values(posBaseListGroupByPool).filter((filterItem: any) => {
      if (firstTokenAddress && lastTokenAddress) {
        return (
          (fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == lastTokenAddress) ||
          (fixCoinType(filterItem.tokenA.coin_type) == lastTokenAddress && fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress)
        )
      } else if (firstTokenAddress) {
        return fixCoinType(filterItem.tokenA.coin_type) == firstTokenAddress || fixCoinType(filterItem.tokenB.coin_type) == firstTokenAddress
      } else {
        return filterItem
      }
    })
    console.log('ðŸš€ ~ useEffect ~ filterArr:', Object.values(posBaseListGroupByPool), filterArr)
    setShowPosListGroupByPool(filterArr)
    const posListLength = filterArr.reduce((sum, item: any) => {
      return sum + (item.list ? item.list.length : 0)
    }, 0)
    if (currentAccount?.address) {
      setShowPosListLength(posListLength.toString())
    }
  }, [selectCoinList, posBaseListGroupByPool])

  useEffect(() => {
    // ç»„ä»¶å¸è½½æ—¶ åˆ—è¡¨æ¡æ•°é‡ç½®
    return () => {
      setShowPosListLength('')
    }
  }, [])
  useEffect(() => {
    if (posBaseListLoading) {
      setShowPosListLength('')
    }
  }, [posBaseListLoading])

  const tabList = useMemo(() => {
    return [
      {
        label: 'Positions',
        value: 'positions',
        num: Number(showPosListLength)
      }
      // {
      //   label: 'Vaults',
      //   value: 'vaults',
      //   num: 12
      // }
    ]
  }, [showPosListLength])
  const [currentTab, setCurrentTab] = useState('positions')

  return (
    <Block p="0 16px 16px" bg="block_color_opacity.50" backdropFilter="blur(20px)">
      <VStack gap="16px" w="100%">
        <HStack w="100%" justify="space-between">
          <ProfileMenus
            type="tab"
            currentTab={currentTab}
            tabs={tabList}
            onTabChange={tab => setCurrentTab(tab.value)}
            textStyle={{
              fontSize: '16px'
            }}
            wrapStyle={{
              bg: 'none'
            }}
          />
          <PoolsFilter
            isProfile={true}
            isPools={false}
            handleRefresh={() => {}}
            selectCoinList={selectCoinList}
            onClickSelectCoinList={onClickSelectCoinList}
            onDeleteSelectCoinList={onDeleteSelectCoinList}
          />
        </HStack>
        <Box h="1px" w="100%" bg="border" mt="-16px" />
        <LiquidityAndYield />
        {!currentAccount?.address ? (
          <NoData type="nowallet" onboard={() => onWalletModal(true)} noBorder />
        ) : !isAutoRefresh && posBaseListLoading ? (
          [{}, {}, {}, {}].map((item, index) => {
            return <PositionListLoading key={index} />
          })
        ) : Object.values(showPosListGroupByPool)?.length > 0 ? (
          Object.values(showPosListGroupByPool).map((item, index) => {
            return <PoolItem key={index} poolInfo={item} />
          })
        ) : (
          <NoData type="nodata" text="No liquidity positions" noBorder />
        )}
      </VStack>
    </Block>
  )
}

export default ProfileLiquidity
