import { Block } from '@cetus/design'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { formatPrice } from '@cetus/utils'
import { Skeleton, Text, VStack } from '@chakra-ui/react'
import Header from './Header'
type HoldingType = 'cetus' | 'xcetus'
const TypeMap: Record<HoldingType, { title: string; imgUrl: string; bgUrl: string }> = {
  cetus: {
    title: 'CETUS',
    imgUrl: envConfigs.cetus_coin.logo_url as string,
    bgUrl: '/images/cetus_holding_bg.png'
  },
  xcetus: {
    title: 'xCETUS',
    imgUrl: envConfigs.x_cetus_coin.logo_url as string,
    bgUrl: '/images/xcetus_holding_bg.png'
  }
}

type HoldingProps = {
  amount: string
  amountUSD: string
  type: 'cetus' | 'xcetus'
  isLoading: boolean
  onClick?: () => void
  btnTitle?: string
  children?: React.ReactNode
}
function Holding({ type, amount, amountUSD, isLoading, onClick, btnTitle, children }: HoldingProps) {
  const { title, imgUrl, bgUrl } = TypeMap[type]
  return (
    <Block
      flex="1"
      bg={`right 0 bottom 60% / 86px 142px no-repeat url("${bgUrl}"), radial-gradient(40% 74% at 100% 100%,#1D342E 0%, rgba(22,27,48,0) 100%), rgba(0,0,0,0.5)`}
      backdropFilter="blur(20px)"
      p="16px"
      borderRadius="16px"
    >
      <VStack w="100%" gap="0">
        <Header title={`${title} Holdings`} imgUrl={imgUrl} onClick={onClick} btnTitle={btnTitle} />
        <VStack align="flex-start" w="100%" m="31px 0 60px">
          <Skeleton isLoaded={!isLoading} h="26px">
            <Text color="text_caption" h="26px" lineHeight="26px" fontSize="22px">
              {amount}
            </Text>
          </Skeleton>
          <Skeleton isLoaded={!isLoading} h="17px">
            <Text h="17px" lineHeight="17px" fontSize="14px">
              Total {title} (â‰ˆ ${formatPrice(amountUSD, 2)})
            </Text>
          </Skeleton>
        </VStack>
        {children}
      </VStack>
    </Block>
  )
}

export default Holding
