import { CoinInfoBlock } from '@/components/limit/OrderItemBlock/CoinInfoBlock'
import { ExpiryBlock } from '@/components/limit/OrderItemBlock/ExpiryBlock'
import { FilledSizeBlock } from '@/components/limit/OrderItemBlock/FilledSizeBlock'
import { OrderActionBlock } from '@/components/limit/OrderItemBlock/OrderActionBlock'
import { PriceBlock } from '@/components/limit/OrderItemBlock/PriceBlock'
import { StatusBlock } from '@/components/limit/OrderItemBlock/StatusBlock'
import useGetLimitOrderHistory from '@/hooks/limit/useGetLimitOrderHistory'
import useLimitCancelAction from '@/hooks/limit/useLimitCancelAction'
import useLimitListStore from '@/store/limit/useLimitList'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import { LimitOrderInfo } from '@/types/limit'
import { SelectTab } from '@cetus/design'
import { useRpcListener } from '@cetus/hooks'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { H5MapTable, NoData, Table } from '@cetus/ui-kit'
import { formatNumber } from '@cetus/utils'
import { Button, HStack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'

function Limit() {
  const { currentAccount } = useAccountStore()
  const { getLimitOrderHistory, historyOrderList, historyOrderLoading } = useGetLimitOrderHistory()
  const { myOrderList, orderListLoading } = useLimitListStore()
  const { isAutoRefresh, autoRefreshCount } = useActiveOrdersStore()

  const [currentTab, setCurrentTab] = useState('openOrders')
  const tabList = useMemo(() => {
    return [
      {
        label: 'Open Orders',
        value: 'openOrders',
        num: !isAutoRefresh && orderListLoading ? '' : myOrderList?.length
      },
      {
        label: 'Orders History',
        value: 'ordersHistory',
        num: !isAutoRefresh && historyOrderLoading ? '' : historyOrderList?.length
      }
    ]
  }, [orderListLoading, historyOrderLoading, myOrderList, historyOrderList])

  const getOrderList = (isLoading?: boolean) => {
    // if (currentTab == 'openOrders') return
    if (currentAccount?.address) {
      getLimitOrderHistory(currentAccount.address, isLoading)
    }
  }
  useEffect(() => {
    console.log('ðŸš€ ----------------------------------------------------ðŸš€')
    console.log('ðŸš€ ~ useEffect ~ autoRefreshCount:', autoRefreshCount)
    console.log('ðŸš€ ----------------------------------------------------ðŸš€')
    if (autoRefreshCount > 0) {
      getOrderList(true)
    }
  }, [autoRefreshCount])

  useEffect(() => {
    getOrderList(true)
  }, [currentTab, currentAccount])

  const currentLoading = useMemo(() => {
    if (currentTab == 'openOrders') {
      return orderListLoading
    } else {
      return historyOrderLoading
    }
  }, [currentTab, orderListLoading, historyOrderLoading])

  const currentList = useMemo(() => {
    console.log('ðŸš€ ~ currentList ~ myOrderList:', myOrderList, historyOrderList)
    if (currentTab == 'openOrders') {
      return myOrderList
    } else {
      return historyOrderList
    }
  }, [currentTab, myOrderList, historyOrderList])

  useRpcListener({
    onRpcChange: () => {
      getOrderList(true)
    }
  })

  const { isApp } = useWindowWidth()

  const { handleCancelOrder, cancelOrderLoading } = useLimitCancelAction()
  const { getExplorerUrl } = useExplorer()
  return (
    <VStack w="100%" gap="12px">
      <HStack w="100%" justify="space-between">
        <SelectTab
          type="outlineTab"
          tabList={tabList}
          currentTab={currentTab}
          handleChangeTab={(item: any) => setCurrentTab(item?.value)}
          isActive={(current, tab) => current === tab.value}
          wrapStyle={{
            border: 'none',
            h: '32px',
            gap: '8px',
            bg: 'none'
          }}
          itemStyle={{
            fontSize: '14px',
            borderRadius: '8px',
            p: '0 8px'
          }}
        />
        {currentList?.length > 1 && currentTab == 'openOrders' && (
          <HStack>
            {/* <Button h="32px" p="8px" fontSize="12px" fontWeight="500" borderRadius="8px">
              Claim All
            </Button> */}
            <Button
              h="32px"
              p="8px"
              fontSize="12px"
              fontWeight="400"
              variant="ghost"
              isLoading={cancelOrderLoading}
              onClick={() => {
                handleCancelOrder(myOrderList)
              }}
            >
              Cancel All
            </Button>
          </HStack>
        )}
      </HStack>
      <VStack w="100%" position="relative" gap="20px">
        {!currentLoading && currentList?.length === 0 ? (
          <NoData type="nodata" noBorder />
        ) : isApp ? (
          <VStack w="100%" gap="4px">
            <H5MapTable
              rowKey="order_id"
              columns={getColumns(currentTab == 'openOrders')}
              dataSource={currentList}
              loading={!isAutoRefresh && currentLoading}
              itemSkeletonLength={5}
              itemHeight="30px"
            />
          </VStack>
        ) : (
          <Table
            rowKey="order_id"
            columns={getColumns(currentTab == 'openOrders')}
            dataSource={currentList}
            loading={!isAutoRefresh && currentLoading}
            rowStyle={{ h: '80px', cursor: 'pointer' }}
          />
        )}
      </VStack>
    </VStack>
  )
}

const getColumns = (isOpenOrder: boolean) => {
  const columns = [
    {
      title: <Text>Order</Text>,
      key: 'order',
      thConfig: { w: '20%' },
      render: (record: LimitOrderInfo) => <CoinInfoBlock imgSize="28px" info={record} isProfile={true} />
    },
    {
      title: <Text textAlign="right">Limit Price</Text>,
      key: 'price',
      thConfig: { w: '20%' },
      render: (record: LimitOrderInfo) => <PriceBlock info={record} isProfile={true} />
    },
    {
      title: <Text textAlign="right">Total</Text>,
      key: 'total',
      thConfig: { w: '16%' },
      render: ({ total_pay_amount, pay_coin }: { total_pay_amount: string; pay_coin: Token }) => (
        <Text color="text_caption">
          {formatNumber(total_pay_amount)} {pay_coin.symbol}
        </Text>
      )
    },
    {
      title: <Text textAlign="right">Filled Size</Text>,
      key: 'completed',
      thConfig: { w: '12%' },
      render: (record: LimitOrderInfo) => <FilledSizeBlock info={record} isProfile={true} />
    },
    {
      title: <Text textAlign="right">Expiry</Text>,
      key: 'expiry',
      thConfig: { w: '16%' },
      render: (record: LimitOrderInfo) => <ExpiryBlock info={record} isProfile={true} />
    },
    !isOpenOrder && {
      title: <Text textAlign="right">Status</Text>,
      key: 'status',
      thConfig: { w: '8%' },
      render: (record: LimitOrderInfo) => <StatusBlock historyInfo={record} openExpendItemObj={undefined} isProfile={true} />
    },
    {
      title: <Text textAlign="right">{isOpenOrder ? 'Actions' : 'Action'}</Text>,
      key: 'actions',
      thConfig: { w: '8%' },
      render: (record: LimitOrderInfo) => <OrderActionBlock orderInfo={record} isProfile={true} isOpenOrder={isOpenOrder} />
    }
  ]

  return columns.filter(Boolean)
}

export default Limit
