import { useXCetus } from '@/hooks/xcetus/useXCetus'
import useProfileXCetusStore from '@/store/profile/xcetus'
import { HStack, VStack } from '@chakra-ui/react'
import CetusHolding from './CetusHolding'
import RedeemList from './RedeemList'
import Rewards from './Rewards'
import XCetusHolding from './XCetusHolding'
import XCetusModal from './XCetusModal'

function XCetus() {
  const { isXCetusModalOpen, setIsXCetusModalOpen } = useProfileXCetusStore()
  const { availableXCetusAmount, redeemingXCetusAmount, cetusApr, myShare, totalRewardValue, nextStartTime } = useXCetus()
  return (
    <VStack w="100%" gap="12px">
      <HStack w="100%" justify="space-between" gap="12px">
        <Rewards cetusApr={cetusApr} myShare={myShare} totalRewardValue={totalRewardValue} nextStartTime={nextStartTime} />
        <XCetusHolding availableXCetusAmount={availableXCetusAmount} redeemingXCetusAmount={redeemingXCetusAmount} />
        <CetusHolding />
      </HStack>
      <RedeemList />
      {isXCetusModalOpen && (
        <XCetusModal isOpen={isXCetusModalOpen} onClose={() => setIsXCetusModalOpen(false)} availableXCetusAmount={availableXCetusAmount} />
      )}
    </VStack>
  )
}

export default XCetus
