import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { Input, InputGroup, InputRightElement } from '@chakra-ui/react'
import { useRef, useState } from 'react'
type SearchAssetsInputProps = {
  inputValue: string | number | readonly string[] | undefined
  changeInputValue: (value: string) => void
}
function SearchAssetsInput({ inputValue, changeInputValue }: SearchAssetsInputProps) {
  const inputRef = useRef<HTMLInputElement>(null)
  const [inputFocus, setInputFocus] = useState(false)
  const { isApp } = useWindowWidth()
  // useEffect(() => {
  //   if (!isApp) {
  //     // é¡µé¢åŠ è½½åŽè‡ªåŠ¨èšç„¦
  //     inputRef.current.focus()
  //   }
  // }, [isApp])
  return (
    <InputGroup>
      <Input
        w={inputFocus ? '203px' : '32px'}
        transition="width 0.1s ease"
        _hover={{
          bg: 'bg_secondary'
        }}
        bg={'bg_secondary'}
        ref={inputRef}
        borderRadius="8px"
        h="32px"
        border="1px solid"
        borderColor="border"
        variant="outline"
        value={inputValue}
        onChange={e => {
          changeInputValue(e.target.value)
        }}
        onFocus={() => {
          setInputFocus(true)
        }}
        onBlur={() => {
          changeInputValue('')
          setInputFocus(false)
        }}
        fontWeight="500"
        textAlign="left"
        fontSize="12px"
        // placeholder="Search by token or address"
        pr={'8px'}
        pl={inputFocus ? '28px' : '8px'}
      />
      <InputRightElement>
        <Icon
          position="absolute"
          top="6px"
          left={inputFocus ? '-198px' : '-26px'}
          xlinkHref="#icon-icon_search"
          svgHover="text_paragraph"
          onClick={e => {
            inputRef.current?.focus()
            setInputFocus(true)
          }}
        />
        {inputValue ? (
          <Icon
            xlinkHref="#icon-icon_close"
            position="absolute"
            top="6px"
            right={'6px'}
            onMouseDown={e => e.preventDefault()}
            onClick={e => {
              changeInputValue('') // ç›´æŽ¥æ“ä½œ DOM æ¸…ç©ºå€¼
              setTimeout(() => {
                inputRef.current?.focus() // å»¶è¿Ÿèšç„¦ï¼Œç¡®ä¿ç„¦ç‚¹ä¸ä¸¢å¤±
              }, 0)
            }}
          />
        ) : null}
      </InputRightElement>
    </InputGroup>
  )
}

export default SearchAssetsInput
