import useProfileXCetusStore from '@/store/profile/xcetus'
import useXCetusStore from '@/store/xcetus/useXCetus'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { useAccountStore } from '@cetus/stores'
import useTokenPriceStore from '@cetus/stores/src/tokenPrice'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { HTextLabelBox } from '@cetus/ui-kit'
import { formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import Holding from './Holding'

type Props = {
  availableXCetusAmount: string
  redeemingXCetusAmount: string
}

function XCetusHolding({ availableXCetusAmount, redeemingXCetusAmount }: Props) {
  const { currentAccount } = useAccountStore()
  const { veNFT, veNFTLoading, lockCetusListLoading } = useXCetusStore()
  const { getTokenAmountValue } = useTokenPrice()
  const { coinPriceObj } = useTokenPriceStore()
  const amountUSD = useMemo(() => {
    return getTokenAmountValue(
      envConfigs.cetus_coin.coin_type,
      fromDecimalsAmountFix(veNFT?.xcetus_balance || '0', envConfigs.x_cetus_coin.decimals)
    ).toString()
  }, [veNFT?.xcetus_balance, currentAccount?.address, coinPriceObj])

  const { setCurrentXCetusTab, setIsXCetusModalOpen } = useProfileXCetusStore()

  const onClickRedeem = () => {
    setIsXCetusModalOpen(true)
    setCurrentXCetusTab('Redeem CETUS')
  }

  const isLoading = (lockCetusListLoading || veNFTLoading) && veNFT !== undefined

  return (
    <Holding
      type="xcetus"
      amount={currentAccount ? formatNumber(fromDecimalsAmountFix(veNFT?.xcetus_balance || '0', 9), 9).toString() : '--'}
      amountUSD={amountUSD}
      onClick={onClickRedeem}
      btnTitle="Redeem"
      isLoading={isLoading}
    >
      <VStack w="100%" gap="16px" h="48px">
        <HTextLabelBox
          isLoading={isLoading}
          label="Available"
          value={currentAccount ? formatNumber(fromDecimalsAmountFix(availableXCetusAmount, 9), 9).toString() : '--'}
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px', h: '14px' }}
        />
        <HTextLabelBox
          isLoading={isLoading}
          label="Redeeming"
          value={currentAccount ? formatNumber(fromDecimalsAmountFix(redeemingXCetusAmount, 9), 9).toString() : '--'}
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px', h: '14px' }}
        />
      </VStack>
    </Holding>
  )
}

export default XCetusHolding
